


import './index.scss'
import { useEffect, useRef, useState } from 'react'
import axios from 'axios'
import Logo from '../img/logo.svg'

import RedP from '../img/RedP.svg'
import FaceP from '../img/FaceP.svg'
import InstaP from '../img/InstaP.svg'

import gsap from 'gsap'

const HomeDos = () =>{
    const[estadoBoton ,setEstadoBoton] = useState(false)
    const[Texto ,setTexto] = useState('')
    const tl = gsap.timeline()
    const ventanaSubmenu = useRef(null);
    const elementoSubmenu1 = useRef(null);
    const elementoSubmenu2 = useRef(null);
    const elementoSubmenu3 = useRef(null);
    const elementoSubmenu4 = useRef(null);
    const elementoSubmenu5 = useRef(null);
    const elementoSubmenu6 = useRef(null);

    function Boton(){
        let Duracion = 0.4
        if(estadoBoton === false){
            tl.add('openbutton')
            .from(ventanaSubmenu.current, { display:"none", minHeight: "5vh",  duration:Duracion , ease: 'circ.out'}, 'openbutton')        
            .to(ventanaSubmenu.current, { display:"flex",minHeight: "70vh",  duration:Duracion , ease: 'circ.out'}, 'openbutton')        
            .from(elementoSubmenu1.current, { display:"none",  duration:Duracion, delay:Duracion, ease: 'circ.out'}, 'openbutton')        
            .to(elementoSubmenu1.current, { display:"flex", duration:Duracion, delay:Duracion,  ease: 'circ.out'}, 'openbutton')        
            .from(elementoSubmenu2.current, { display:"none",  duration:Duracion, delay:Duracion, ease: 'circ.out'}, 'openbutton')        
            .to(elementoSubmenu2.current, { display:"flex", duration:Duracion, delay:Duracion,  ease: 'circ.out'}, 'openbutton')        
            .from(elementoSubmenu3.current, { display:"none",  duration:Duracion, delay:Duracion, ease: 'circ.out'}, 'openbutton')        
            .to(elementoSubmenu3.current, { display:"flex", duration:Duracion, delay:Duracion,  ease: 'circ.out'}, 'openbutton')        
            .from(elementoSubmenu4.current, { display:"none",  duration:Duracion, delay:Duracion, ease: 'circ.out'}, 'openbutton')        
            .to(elementoSubmenu4.current, { display:"flex", duration:Duracion, delay:Duracion,  ease: 'circ.out'}, 'openbutton')        
            .from(elementoSubmenu5.current, { display:"none",  duration:Duracion, delay:Duracion, ease: 'circ.out'}, 'openbutton')        
            .to(elementoSubmenu5.current, { display:"flex", duration:Duracion, delay:Duracion,  ease: 'circ.out'}, 'openbutton')                    
            .from(elementoSubmenu6.current, { display:"none",  duration:Duracion, delay:Duracion, ease: 'circ.out'}, 'openbutton')        
            .to(elementoSubmenu6.current, { display:"flex", duration:Duracion, delay:Duracion,  ease: 'circ.out'}, 'openbutton')        
            
            
        }
    }


    function SeleccionTexto(newText){
        let Duracion = 0.4
        if(estadoBoton === false){
            tl.add('closebutton')
            tl.to(ventanaSubmenu.current, { display:"none", duration:Duracion , ease: 'circ.out'}, 'closebutton')        
        
            
            setTexto(newText)

        }

    }



    function Enviar(){
        let Nombre =  document.getElementById("Nombre").value
        let Telefono =  document.getElementById("Telefono").value
        let Email =  document.getElementById("Email").value
        let Tratamiento =  document.getElementById("Tratamiento").value
        if(Nombre === ''){
            alert("Ingrese un Nombre por favor" )
            return
        }
        if(Telefono === ''){
            alert("Ingrese un Telefono favor" )
            return
        }
        if(Email === ''){
            alert("Ingrese un Email por favor" )
            return
        }
        if(Tratamiento === ''){
            alert("Elija un Tratamiento por favor" )
            return
        }
        alert("llego aqui 1")

        let nombreEscritorio = Nombre;            
        let telefonoEscritorio = Telefono;           
        let correoEscritorio = Email;   
        let tratamientoEscritorio = Tratamiento;
            
        

        let utm_source =''
        let utm_medium =''
        let utm_campaign =''    
        let valores = (window.location.href)
        valores  =  ((valores.split("/"))[4])
        if(valores.length>3 ){
            valores= valores.replace("?","")
            valores = (valores.split("&"))           
            utm_source = (valores[0].split("="))[1]
            utm_medium = (valores[1].split("="))[1]
            utm_campaign = (valores[2].split("="))[1]           
        }


        alert("llego aqui 2")
             
       alert("Llego aqui")
        
        
        const options = {
            method: 'POST',
            url: 'https://servidor.resvera.com.mx/enviarData',
            headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
            },
            params: {                        
                    email: correoEscritorio ,
                    nombre: nombreEscritorio, 
                    telefono: telefonoEscritorio,
                    zona: tratamientoEscritorio,
                    landing: "landing03",
                    source: utm_source,
                    medium: utm_medium,
                    campaign: utm_campaign                                                       
            }
        };

        axios.request(options)
            .then(function (response) {
                console.log(response.data);
                window.location.href = "https://resvera.com.mx/thk-aparatos-nov/"                        
            })
            .catch(function (error) {
                console.error(error);
            });



    }




    return(
        <div className="containerHomeDos">
            <div className="logoContainer">
                <img src={Logo}></img>
            </div>
            <div className="subtitleContainer">
                <text>
                    <label><strong>Disfruta hoy el lujo</strong></label>
                    <label>de cuidarte con <strong>50% OFF!</strong></label>
                </text>
            </div>
            <div className="formContainer">
                <input type="text"  placeholder='Nombre' id="Nombre" ></input>
                <input type="tel"  placeholder='Telefono' id="Telefono" ></input>
                <input type="m"  placeholder='Correo electronico' id="Email" ></input>
                <input type="text"  placeholder={Texto !=='' ? Texto:"Qu te gustaria hacer por ti?" } value={Texto}  id="Tratamiento" readOnly="readOnly"></input>
                <div className='containerBotonSubmenu' onClick={() => Boton()}>
                    <div className='boton'>
                        <div className="lineaUno"></div>
                        <div className="lineaDos"></div>
                    </div>
                </div>
                   
                       
            </div>
            <div className="buttonContainer" onClick={() => Enviar()}>
                <button  value="Agenda tu cita" >Agenda tu cita</button>
            </div>
            <div className="footerContainer">
                <div className="logos">
                    <img src={RedP}></img>
                    <img src={FaceP}></img>
                    <img src={InstaP}></img>
                </div>
    
            </div>
            <div className="subFormulario">
                <ul ref={ventanaSubmenu}>
                    <li ref={elementoSubmenu1} onClick={()=>SeleccionTexto('Limpieza Profunda + Hidrodermoabrasin')}><div></div><text ><label className='titulo'>Limpieza Profunda + Hidrodermoabrasin</label></text></li>
                    <li ref={elementoSubmenu2} onClick={()=>SeleccionTexto('Petal Peel')}><div ></div><text ><label className='titulo'>Petal Peel: </label><label>Manchas y bolsas en los ojos</label></text></li>
                    <li ref={elementoSubmenu3} onClick={()=>SeleccionTexto('Magnetic Mask')}><div ></div><text ><label className='titulo'>Magnetic Mask: </label><label>Detox, rejuvenecimiento, luminosidad</label></text></li>
                    <li ref={elementoSubmenu4} onClick={()=>SeleccionTexto('Aqua Lift')}><div></div><text><label className='titulo'>Aqua Lift: </label><label>(fototerapia)</label><label>Lineas finas y arrugas</label></text></li>
                    <li ref={elementoSubmenu5} onClick={()=>SeleccionTexto('Lifting')}> <div></div><text><label className='titulo'>Lifting: </label><label>Envejecimiento,  arrugas y luneas de expresion</label></text></li>
                    <li ref={elementoSubmenu6} onClick={()=>SeleccionTexto('Hydratance')}><div></div><text><label className='titulo'>Hydratance: </label><label>Piel seca, bronceada, hidratacin profunda</label></text></li>
                </ul>
            </div>
            <div className="espacioContainer"></div>
        </div>
    )
}

export default HomeDos